.main-container {
    width: calc(100%-20px);
    height: calc(100%-20px);
    border-radius: var(--primer-borderRadius-large, 8px) !important;
    align-items: stretch !important;
    background-color: #1d1d1d;
    color: white;
    border: solid #2d2d2d;
    box-shadow: 0px 0px 60px 5px #000000;
    transition: all 2s ease;
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
}  

.Geometry-container { width: 100%; height: 100%; background-color: red;}


/* For tablets */
@media (min-width: 768px) {

    .main-container {
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;

        max-width: 900px !important;
        height: 500px !important;
        border-radius: var(--primer-borderRadius-large, 8px) !important;
        position: relative !important;
        display: flex !important;
        align-items: stretch !important;
        background-color: #1d1d1d;
        color: white;
        border: solid #2d2d2d;
        box-shadow: 0px 0px 60px 5px #000000;
        transition: all 2s ease;
        margin-bottom: var(--base-size-16, 16px) !important;
    }    
}

/* For desktops */
@media (min-width: 1024px) {

    .main-container {
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;

        max-width: 900px !important;
        height: 500px !important;
        border-radius: var(--primer-borderRadius-large, 8px) !important;
        position: relative !important;
        display: flex !important;
        align-items: stretch !important;
        background-color: #1d1d1d;
        color: white;
        border: solid #2d2d2d;
        box-shadow: 0px 0px 60px 5px #000000;
        transition: all 2s ease;
        margin-bottom: var(--base-size-16, 16px) !important;
    }


    .About-me-container {background-color: blue;}
}

.about-above { margin:20px; font-size: 20px;}
.button-text-container {text-align: center; color: white;}